.card {
  border-radius: 8px;
  z-index: -99;
  display: flex;
  align-items: flex-start;
  overflow: hidden;
  gap: 20px;
}

.card:hover {
  transform: scale(1.03);
}

.card-cover {
  position: relativi;
}

.card-overlay {
  display: none;
}

.card:hover .card-overlay {
  position: absolute;
  display: grid;
  place-items: center;
  border-radius: 8px;
  background: rgba(11,10,26,.7);
  backdrop-filter: grayscale(100%);
  width: 100%;
  height: 100%;
}

.card-overlay svg {
  width: 80px;
}

.card-cover img {
  width: 130px;
  border-radius: 8px;
  aspect-ratio: 11/16;
  object-fit: cover;
}

.title {
  margin-bottom: 10px!important;
}

.card-body {
  display: flex;
  justify-content: space-between;
}

:is(.search, .favorite) .card-body {
  flex-direction: column;
}

.card-body small {
  margin-block: 3px;
}

.body-top {
  display: flex;
  gap: 20px;
  margin: 5px 0!important;
  color: lightgreen;
}

.rating {
  margin-top: 10px;
  background: gold;
  color: #0B0A1A;
  padding: 0 8px;
  border-radius: 99px;
}

.rating:empty::after {
  content: 'N/A';
}

.genre {
  color: rgba(180,180,180,.8);
}
