.sorting-btn * {
  transition: .2s;
}

.sorting-btn {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 10px;
  align-items: center;
}

.sorting-btn input {
  display: none;
}

.sorting-btn label:has(:checked) svg {
  color: #fff;
}

.sorting-btn svg {
  color: rgba(180,180,180,.5);
  width: 20px;
}

.arrow svg {
  color: #fff;
  width: 15px;
  margin-left: 16px;
}

.arrow:has(:checked) svg {
  transform: rotate(180deg);
}
