@font-face {
  font-family: Poppins;
  src: url('Poppins-Medium.otf');
}

@font-face {
  font-family: Poppins-light;
  src: url('Poppins-Regular.otf');
}

* {
  font-family: Poppins;
  box-sizing: border-box;
  -webkit-text-size-adjust: none;
  transition: .3s;
}

body {
  position: relative;
  min-height: 92vh;
  margin: 0;
  background: #0B0A1A;
  color: #fff;
}

body::after {
  content: '';
  display: block;
  height: 80px; /* Set same as footer's height */
}

a {
  text-decoration: none;
  color: #fff;
  -webkit-tap-highlight-color: transparent;
}

.loading {
  height: 80vh;
  display: grid;
  place-items: center;
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }

  4.9% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }

  5% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}


@media screen and (min-width: 600px) {
  .home {
    grid-template-columns: repeat(4,1fr)!important;
  }
}

