/* HOME */
.home {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.home .card {
  flex-direction: column;
  gap: 8px;
}

.hero-section {
  padding: 0 20px;
  margin: 25px 0;
}

.hero-cover  {
  height: 30vh;
}

.hero-cover img {
  border-radius: 20px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}


.card * {
  margin: 0;
}

.card-cover {
  position: relative;
}

.home .card-cover img {
  width: 100%;
}

.home :is(.release, .episode) {
  position: absolute;
  background: rgba(249,68,74,.8);
}

.home .episode {
  top: 0;
  padding: 2px 15px 2px 10px;
  border-bottom-right-radius: 99px;
}

.home .release {
  bottom: 6px;
  right: 0;
  padding: 2px 10px 1px 15px;
  border-top-left-radius: 99px;
  border-bottom-right-radius: 28px;
}

.home .title {
  font-size: .8rem;
  margin-bottom: 0!important;
}


.home :is(.rating, .status, .genre) {
  display: none;
}
/* END OF HOME*/


/* ABOUT */
.about {
  height: 80vh;
  display: grid;
  place-content: center;
  text-align: center;
  padding: 20vw;
}

.about a {
  text-decoration: underline;
  color: lightsteelblue;
}
/* END OF ABOUT */


/* ANIME */ 
body:has(.anime-info) nav {
  display: none;
}

.anime-info {
  margin: auto;
  display: grid;
}

.cover-info {
  position: relative;
  overflow: hidden;;
}

.cover-info img {
  width: 100vw;
  object-fit: cover;
  object-position: top;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  background: linear-gradient(0deg, #0B0A1A, rgba(11,10,26,.8), rgba(0,0,0,0));
}

.top-info {
  position: relative;
}

.top-info small {
  display: flex;
  align-content: center;
  gap: 4px;
  background: rgba(180,180,180, .2);
  padding: 5px 12px 5px 8px;
  border-radius: 8px;
}

.top-info button svg {
  width: 30px;
  color: #fff;
}

.bottom-info {
  padding-top: 10px;
  margin-top: 10px!important;
  border-top: 3px solid rgba(180,180,180,.3);
  display: grid;
  grid-gap: 10px;
}

.bottom-info b:empty:after {
  content: '-';
}

.bottom-info small {
  border-bottom: 1px solid rgba(180,180,180,.1);
  display: grid;
  grid-template-columns: 40% 60%;
  text-align: left;
  color: rgba(180,180,180,.5);
}

small b {
  text-align: right;
  color: #fff;
}

.bottom-info h4 {
  margin-top: 30px!important;
}

.sinopsis {
  display: grid;
  grid-gap: 16px;
}

.sinopsis p {
  margin-top: 0!important;
}

.read-more {
  cursor: pointer;
  margin-bottom: 20px!important;
}

.read-more:hover {
  color: rgba(180,180,180,.95);
}

.cover-info :is(button, label) {
  top: 20px;
  position: absolute;
  border: none;
  width: 40px;
  padding-right: 40px;
  aspect-ratio: 1/1;
  z-index: 99;
  display: grid;
  place-items: center;
  border-radius: 99px;
  background: rgba(11, 10, 26, .4);
  backdrop-filter: blur(8px);
  scale: .8;
}

.share-btn {
  right: 20px;
}

.back-btn {
  left: 20px;
}

.favorite-btn {
  right: 20px;
  top: 80px!important;
  padding-right: 0!important;
}

.favorite-btn input {
  width: 30px;
  height: 30px;
  position: absolute;
  opacity: 0;
}

.favorite-btn:has(input:checked) {
  background: #fff;
  box-shadow: 0 0 20px #f94449;
}

.favorite-btn:has(input:checked) svg {
  fill: #f94449;
}

.cover-info svg {
  width: 30px;
  color: #fff;
}

.body-info svg {
  width: 15px;
}

.star-icon {
  color: goldenrod;
}

.body-info {
  position: relative;
  top: -300px;
  padding: 0 20px;
  display: grid;
}

.body-info * {
  margin: 0;
}

.body-info .genres {
  opacity: .5;
}

.top-info {
  display: flex;
  gap: 15px;
  margin: 10px 0;
}

.body-info p {
  margin-top: 10px;
  font-size: .9rem;
  line-height: 1.4rem;
  font-family: Poppins-Light;
}

.body-info p a {
  color: #f94449;
}

.score:empty::before {
  content: '-';
}

.episodes-info {
  margin-top: 30px;
  display: grid;
  grid-gap: 20px;
}

.episode-card {
  padding: 8px;
  border: 2px solid rgba(180,180,180,.3);
  border-radius: 10px;
}

.episode-card:hover {
  background: #f94449;
  transform: translateX(5px);
}

.episode-card:hover a {
  text-decoration: underline;
}

/* END OF ANIME */ 


/* SEARCH */
.card-container {
  width: 90vw;
  margin: auto;
  display: grid;
  grid-gap: 25px;
}

.input-container {
  display: flex;
  align-items: center;
  padding-right: 10px;
  border: 2px solid #fff;
  width: 87%;
  overflow: hidden;
  border-radius: 9px;
  margin: 20px auto;
}

.nav-link {
  display: grid;
  place-items: center;
}

.input-container svg {
  color: #fff;
  width: 30px;
}

.input-container input {
  width: 100%;
  padding: 10px 20px;
  background: none;
  border: none;
  color: #fff;
}

.input-container:has(input:focus) {
  border: 2px solid #f94449;
  box-shadow: inset 0 0 10px rgba(249,68,74,.8);
}

.input-container input:focus {
  outline: none;
}

.result, .not-found {
  text-align: center;
  padding-bottom: 10px;
}

/* END OF SEARCH */


/* STREAM */ 
.stream {
  width: fit-content;
  position: relative;
}

.stream iframe {
  width: 100vw;
  aspect-ratio: 16/9;
}

.stream *:not(iframe) {
  padding: 0 8px;
}

.next-prev-btn {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 50px;
}

.next-prev-btn a {
  display: grid;
  justify-items: center;
  font-family: Poppins-Light!important;
  opacity: .4;
}

.next-prev-btn a:hover {
  opacity: 1;
}

.next-prev-btn svg{
  width: 35px;
}
/* END OF STREAM */ 


/* FAVORITE */
.favorite {
  margin: 0 20px;
}

.fav-header {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
}
/* END OF FAVORITE */


/* ONGOING */
.pagination {
  display: flex;
  justify-content: center;
  gap: 40px;
  padding: 30px 20px 5px 20px;
}

.pagination svg {
  width: 20px;
}

.pagination a {
  display: grid;
  justify-items: center;
  opacity: .5;
}

.pagination a:hover {
  opacity: 1;
}


/* END OF ONGOING */
