nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline: 20px;
  position: sticky;
  top: 0;
  background: #0B0A1A;
  z-index: 99;
}


.logo h3::after {
  content: 'Flix';
  color: #f94449;
}

.menu {
  display: grid;
  grid-gap: 5px;
}

.nav-btn {
  position: absolute;
  scale: 2;
  opacity: 0;
  z-index: 99;
}

nav:has(.nav-btn:checked) .nav-links {
  left: 0px;
}

.menu:has(.nav-btn:checked) :is(.line-1, .line-3) {
  background: #f94449;
  box-shadow: 0 0 8px #f94449;
}

.menu:has(.nav-btn:checked) .line-1 {
  transform: rotate(45deg) translate(25px, 0);
}

.menu:has(.nav-btn:checked) .line-3 {
  transform: rotate(-45deg) translate(12px, 11px);
  width: 30px;
}

.menu:has(.nav-btn:checked) .line-2 {
  opacity: 0;
}

.menu:not(.nav-btn) *:not(.nav-btn) {
  display: inline-block;
  background: #fff;
  height: 4px;
  border-radius: 99px;
}

.line-1, .line-2 {
  width: 30px;
}

.line-3 {
  width: 15px;
}

.nav-search svg {
  width: 30px;
  color: #fff;
}

.nav-links {
  position: fixed;
  top: 49.5px;
  left: -120px;
  height: 100vh;
}

ul {
  padding-left: 0;
  padding-top: 150px;
  display: grid;
  background: #0B0A1A;
  backdrop-filter: blur(8px);
  align-content: start;
  grid-gap: 100px;
}

.nav-links a {
  color: rgba(180,180,180,.4);
  text-decoration: none;
  transform: rotate(-90deg) translateX(70px);
  display: flex;
  align-items: center;
}

.nav-links a:nth-child(2) {
  margin-top: 20px;
  margin-bottom: 10px;
}

.nav-links svg {
  width: 18px;
  margin-right: 10px;
}

a.active {
  color: #fff;
}

.fullscreen {
  background: none;
  border: none;
  background: #0B0A1A;
}

.fullscreen svg {
  width: 20px;
  color: #fff;
}
